img.navbar__avatar {
  border-radius: 50%;
  border: 1px solid white; /* Параметры границы */
  width: 60px;
  height: 60px;
}

.navbar__dropdown {
  background: linear-gradient(40deg, #2096ff, #05ffa3) !important;
  padding: 1.25rem 0.5rem 0.5rem 0.3rem !important;
  filter: brightness(80%);
  //max-height: 40px;
  //background: linear-gradient(40deg, #2096ff, #05ffa3) !important;
}

span.main__title {
  font-family: "Roboto", sans-serif;
  font-size: 0.9rem;
  font-weight: 500;
  color: #d6f0f2;
}

div.navbar__main {
  min-width: 320px;
}

button.link-logout {
  font-weight: 300;
  padding-top: 0.5rem;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #fff;

  &:hover, &:focus {
    text-decoration: none;
  }
}
